.radius {
    border-radius: 10px;
}

.card-icon {
    width: 30px;
    height: 30px;
}

.no-border {
    border: 0;
}

.line-style {
    border-top: 0;
    border-left: 0;
    border-right: 0;
    background-color: transparent;
    border-bottom-color: white;
    margin-bottom: 1em;
    box-shadow: none;
    color: white;
}

.background-map {
    background-image: url(https://i.ibb.co/PjbXN6t/maps.png);
}
.img-car {
    margin-top: 130%;
}

.service-request {
    height: 50vh;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    background-color: #338F94 !important;
}

.init-hidden {
    display: none;
}

::-webkit-input-placeholder {
    color: white !important;
  }