.background-login {
    background: linear-gradient(180deg, #1D5153 0%, #2F8489 100%);
    height: 100%;
}
.background-landing {
    background: linear-gradient(180deg, #338F94 0%, #67C6CB 100%);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.button-laaps {
    background-color: #338F94;
}
.slogan{
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    color: #FFFFFF;
    text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    transform: rotate(0.06deg);
}
.button-subscribe {
    background-color: #338F94;
}
.section {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.men-landing {
    background-color: white;
}

.line-style {
    border-top: 0;
    border-left: 0;
    border-right: 0;
    background-color: transparent;
    border-bottom-color: white;
    margin-bottom: 1em;
    box-shadow: none;
    color: white;
}